.grid {
  box-sizing: border-box;
  padding: 0 .5rem;
}
.grid--container {
  margin: 0 auto;
  max-width: 1200px;
  width: 100%;
}
.grid--no-gutters {
  padding: 0;
}
.grid__row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 100%;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  margin: -1rem -0.5rem 1rem -0.5rem;
  padding: 0;
}
.grid__row:last-child {
  margin-bottom: 0;
}
.grid__row--no-gutters {
  margin: 0;
}
.grid__col {
  box-sizing: border-box;
  flex: 0 0 100%;
  max-width: 100%;
  padding: 1rem .5rem 0 .5rem;
}
.grid__col--no-gutters {
  padding: 0;
}
.grid__row--xs-top {
  align-items: flex-start;
}
.grid__row--xs-center {
  align-items: center;
}
.grid__row--xs-bottom {
  align-items: flex-end;
}
.grid__col--xs-first {
  order: -1;
}
.grid__col--xs-last {
  order: 1;
}
.grid__row--xs-reverse {
  flex-direction: row-reverse;
}
.grid__col--xs-flex {
  display: flex;
}
.grid__text--xs-right {
  text-align: right;
}
.grid__text--xs-left {
  text-align: left;
}
.grid__text--xs-center {
  text-align: center;
}
